<template>
    <!-- 选择生日 -->
    <view class="box-in">
        <view :style="{
            'opacity': requestParam.birthday == '' ? '.5' : '1',
        }" class="box-in-in-2-title">
            选择生日
        </view>
        <view @click="open()" class="box-in-in-2" style="
                                    position: relative;
                                    height: 70rpx;
                                    width: calc(100% - 30rpx);">
            <view style="height: 10rpx;"></view>
            <image class="box-in-in-icon" :src="$kt.file.byPath('/image/icon/calendar.svg')"></image>
            <text class="box-in-in-text"
            v-if="requestParam.birthday == ''"
            style="color:#aaa;font-size: 30rpx;
                                        position: absolute;top:50%;left: 50%;transform: translate(-50%,-50%);
                                        "
            >选择生日</text>
            <text class="box-in-in-text"
            v-if="requestParam.birthday != ''"
            style="
            color:#333;
            font-size: 30rpx;
            position: absolute;top:50%;left: 50%;
            transform: translate(-50%,-50%);
            "
            >{{$kt.util.dateFromat(requestParam.birthday,"yyy-MM-dd")}}</text>

        </view>
        <u-datetime-picker
         ref="datetimePicker"
        :visibleItemCount="10" 
        :show="show"
        :minDate="0"
        :maxDate="maxDate"
        :closeOnClickOverlay="true"
        @close="close"
        @change="change"
        @confirm="confirm"
        :cancelText="'-'"
        mode="date" ></u-datetime-picker>
    </view>
</template>

<script>
export default {
    data() {
        return {
            show: false,
            maxDate: 0,
            requestParam: {
                birthday: ''
            },
            openNum: 0
        }
    },
    onReady() {
    },
    created() {
        // 获取当前时间减去18年
        let date = new Date();
        date.setFullYear(date.getFullYear() - 18);
        this.maxDate = date.getTime();
    },
    methods: {
        open() {
            this.show = true;
        },
        change(e) {
            this.requestParam.birthday = e.value;
            this.$emit("change", this.requestParam.birthday);
        },
		confirm(e){
            this.requestParam.birthday = e.value;
            this.$emit("change", this.requestParam.birthday);
            this.show = false;			
		},
        close() {
            this.show = false;
        }
    }
}
</script>

<style lang="scss" scoped>
.box-in {
    margin-bottom: 30rpx;
    text-align: center;

    .box-in-title {
        font-size: 30rpx;
        font-weight: bold;
        color: #333;
        margin-bottom: 10rpx;
    }

    .box-in-in {
        background-color: #f5f5f5;
        border-radius: 10rpx;
        padding: 10rpx;

        .input-box-input {
            width: 100%;
            padding: 10rpx 0rpx 10rpx 0rpx;
            font-size: 30rpx;
            color: #333;
            border: none;
            outline: none;
            font-weight: bold;
            // 文字间距
            letter-spacing: 2rpx;
        }

    }

    .box-in-in-2-title {
        font-size: 30rpx;
        color: #666;
        text-align: left;
        margin-bottom: 20rpx;
        // margin-left: 10rpx;
    }

    .box-in-in-2 {
        width: calc(50% - 50rpx);
        display: inline-block;
        border: 5rpx solid #f0f0f0;
        padding: 10rpx;
        border-radius: 20rpx;

        .box-in-in-icon {
            width: 40rpx;
            height: 40rpx;
            position: absolute;
            width: 50rpx;
            height: 50rpx;
            left: 20rpx;
            top: 50%;
            transform: translateY(-50%);
        }
    }

    .box-in-in-3 {
        border-radius: 10rpx;
        padding: 10rpx;
        margin-top: 30rpx;

        .box-in-in-3-title {
            font-size: 30rpx;
            font-weight: bold;
            color: #333;
            margin-bottom: 20rpx;
        }

        .box-in-in-3-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
        }

        .box-in-in-3-list-item {
            width: calc(33.33% - 20rpx);
            margin-bottom: 20rpx;
            border: 5rpx solid #f0f0f0;
            padding: 10rpx;
            border-radius: 20rpx;
            text-align: center;
            box-sizing: border-box;
        }

        .box-in-in-3-list-item-text {
            font-size: 30rpx;
            font-weight: bold;
            color: #333;
            opacity: .5;
        }
    }

}
</style>